.contentContainer {
  width: 80%;
  margin: 0 auto; }

.land {
  fill: #cbd9db;
  stroke: white;
  stroke-width: 1.5;
  stroke-miterlimit: 4; }
  .land.valid {
    transition: opacity 0.5s cubic-bezier(0.075, 0.82, 0.165, 1); }
    .land.valid:hover {
      opacity: 0.7; }

