#svg-1 {
  width: 50%;
  overflow: visible; }

#country-name {
  transition: opacity 0.4s cubic-bezier(0.47, 0, 0.745, 0.715);
  opacity: 0; }

.lightBack, #tooltipBackground {
  fill: rgba(190, 190, 190, 0.31);
  stroke: #0000007a; }

tspan.legend {
  dominant-baseline: unset; }

#mapContainer {
  padding: 0 0 0.1rem;
  overflow: hidden; }

.ui.cards > .card {
  margin-bottom: 0;
  margin-left: 2em;
  width: 100%; }
  .ui.cards > .card:last-of-type {
    margin-bottom: 1.5rem; }
  .ui.cards > .card:first-of-type {
    margin-top: 1.5rem; }
  .ui.cards > .card > .main.content {
    padding-top: 0.5rem;
    padding-bottom: 0.3rem; }
  .ui.cards > .card > .extra.content {
    padding: 0.3rem 0; }
    .ui.cards > .card > .extra.content > .description {
      width: 70%;
      text-align: left;
      margin: 0 auto; }

/*.toggle.button{
	height: min-content;
