#svgColumn {
  transition: all 1s linear; }
  #svgColumn.svgFocus {
    padding-left: 22em;
    transition: all 1s linear; }

#provStatsContainer {
  margin-bottom: 0;
  overflow-x: visible;
  overflow-y: scroll;
  height: 100%;
  transition: all 1s linear; }
  #provStatsContainer.scroll-pane {
    overflow-x: visible;
    overflow-y: auto; }
  #provStatsContainer.sidebarHidden {
    width: 0 !important;
    transition: all 1s linear; }

@media only screen and (max-width: 767px) {
  [class*="mobile hidden"],
  [class*="or lower hidden"] {
    display: none !important; } }

#mapContainer #hideIcon {
  transition: all 1s linear; }

#mapContainer .jspDrag {
  border-radius: 25px; }

#mapContainer .jspVerticalBar .jspArrow {
  height: 0; }

#mapContainer .jspTrack {
  border-radius: 25px; }

#filterColumn .filter {
  margin-right: 3.5rem !important; }
